@import url(https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300;600&family=Poppins&display=swap);
:root {
	--first-font   : "Alata", sans-serif;
	--second-font  : "Crimson Text", sans-serif;
	--font-banner  : 'Montserrat', sans-serif;
	--font-poppins : 'Poppins', sans-serif;


	/*------COLOR-----------*/
	--color-orange : #F64C08;
	--color-black  : #000000;
	--color-white  : #FFFFFF;
}

body {
	background-color : #000000;
	background-color : var(--color-black);
}

a {
	text-decoration : none !important;
	color           : #FFFFFF;
	color           : var(--color-white);
}

* {
	margin     : 0;
	padding    : 0;
	box-sizing : border-box;
}

.header {
	display          : -webkit-flex;
	display          : flex;
	-webkit-align-items      : center;
	        align-items      : center;
	color            : #FFFFFF;
	background-color : #000000;
	background-color : var(--color-black);
	position         : -webkit-sticky;
	position         : sticky;
	top              : 0;
	z-index          : 200;
}

.header-left {
	-webkit-flex : 0.4 1;
	        flex : 0.4 1;
}

.header-left h1 {
	text-align     : center;
	font-family    : "Alata", sans-serif;
	font-family    : var(--first-font);
	letter-spacing : 1px;
	padding        : 20px 0;
}

.header-left h1 > span {
	color : #F64C08;
	color : var(--color-orange);
}

.header-right {
	display         : -webkit-flex;
	display         : flex;
	-webkit-align-items     : center;
	        align-items     : center;
	-webkit-justify-content : space-evenly;
	        justify-content : space-evenly;
	-webkit-flex            : 0.6 1;
	        flex            : 0.6 1;
}

.header-right a h4 {
	font-size      : 18px;
	font-family    : "Crimson Text", sans-serif;
	font-family    : var(--second-font);
	cursor         : pointer;
	transition     : -webkit-transform 200ms ease-in;
	transition     : transform 200ms ease-in;
	transition     : transform 200ms ease-in, -webkit-transform 200ms ease-in;
	padding        : 20px 0;
	letter-spacing : 1px;
}

.header-right a h4:hover {
	-webkit-transform        : scale(1.09);
	        transform        : scale(1.09);
	background-color : #F64C08;
	background-color : var(--color-orange);
	color            : #FFFFFF;
	color            : var(--color-white);
	padding          : 5px 20px;
	letter-spacing   : 1px;
	font-size        : 18px;
	cursor           : pointer;
}


/* banner */
.top-container {
	background : url(https://wallpapercave.com/wp/wp4254894.jpg) no-repeat center;
	height     : 100vh;
	color      : #FFFFFF;
	color      : var(--color-white);
	position   : relative;
}

.top-content {
	position  : absolute;
	top       : 50%;
	right     : 150px;
	-webkit-transform : translate(0px, -60%);
	        transform : translate(0px, -60%);
}

.top-content h1 {
	font-size   : 50px;
	font-family : 'Montserrat', sans-serif;
	font-family : var(--font-banner);
}

.top-content p {
	letter-spacing : 1px;
	text-align     : left;
	padding-top    : 5px;
	font-size      : 26px;
	padding-bottom : 20px;
}

.top-content div {
	margin-bottom : 20px;
	font-size     : 18px;
	width         : 550px;
	font-family   : 'Poppins', sans-serif;
	font-family   : var(--font-poppins);
}

.fill {
	color            : #FFFFFF;
	color            : var(--color-white);
	background-color : #F64C08;
	background-color : var(--color-orange);
	font-size        : 14px;
	padding          : 10px 30px;
	font-family      : 'Montserrat', sans-serif;
	font-family      : var(--font-banner);
	font-weight      : 300;
	margin-right     : 20px;
	border           : 0;
	cursor           : pointer;
	width            : 125px;
}

.fill:hover {
	color            : #F64C08;
	color            : var(--color-orange);
	background-color : #FFFFFF;
	background-color : var(--color-white);
	font-weight      : 600;
}

.line {
	color            : #FFFFFF;
	color            : var(--color-white);
	background-color : transparent;
	box-shadow       : inset 0 0 0 1px #FFFFFF;
	box-shadow       : inset 0 0 0 1px var(--color-white);
	font-size        : 14px;
	padding          : 10px 30px;
	font-family      : 'Montserrat', sans-serif;
	font-family      : var(--font-banner);
	font-weight      : 300;
	border           : 0;
	cursor           : pointer;
	width            : 125px;
}

.line:hover {
	background-color : #FFFFFF;
	background-color : var(--color-white);
	color            : #000000;
	color            : var(--color-black);
	font-weight      : 600;
}

/*banner*/

/*Skill*/
.skill-warpper {
	display     : -webkit-flex;
	display     : flex;
	-webkit-align-items : center;
	        align-items : center;
	margin      : 200px 0;
}

.skill-img {
	-webkit-flex : 0.4 1;
	        flex : 0.4 1;
}

.skill-img img {
	width         : 600px;
	object-fit    : contain;
	border-radius : 5px;
	max-width     : 100%;

}

.skill-content {
	-webkit-flex       : 0.6 1;
	        flex       : 0.6 1;
	text-align : left;
	color      : #FFFFFF;
	color      : var(--color-white);
}

.skill-content > div {
	max-width : 500px;
}

.skill-content h2 {
	font-size : 30px;
}

.skill-sets h5 {
	font-family : 'Montserrat', sans-serif;
	font-family : var(--font-banner);
	font-weight : 300;
	padding     : 10px 0 30px 0;
}

.skill-slider > .MuiLinearProgress-colorPrimary {
	background-color : rgb(88, 88, 88);
}

.skill-slider1 > div > .MuiLinearProgress-barColorPrimary {
	background-color : tomato;
}

.skill-slider2 > div > .MuiLinearProgress-barColorPrimary {
	background-color : rgb(255, 145, 0);
}

.skill-slider3 > div > .MuiLinearProgress-barColorPrimary {
	background-color : rgb(228, 213, 0);
}

.skill-slider4 > div > .MuiLinearProgress-barColorPrimary {
	background-color : rgb(136, 146, 0);
}

.skill-slider5 > div > .MuiLinearProgress-barColorPrimary {
	background-color : rgb(255, 209, 6);
}

/*Skill*/


/* Product */
.project-container {
	display         : -webkit-flex;
	display         : flex;
	-webkit-flex-direction  : column;
	        flex-direction  : column;
	-webkit-align-items     : center;
	        align-items     : center;
	-webkit-justify-content : center;
	        justify-content : center;
	margin          : 200px auto;
	color           : #FFFFFF;
	color           : var(--color-white);
}

.project-container h1 {
	padding-bottom : 15px;
}

.project-container p {
	padding-bottom : 20px;
}

.project-container-project {
	display    : -webkit-flex;
	display    : flex;
	margin-top : 50px;
	-webkit-flex-wrap  : wrap;
	        flex-wrap  : wrap;
}

.project-container-project > a {
	width           : 33%;
	padding         : 15px;
	display         : -webkit-flex;
	display         : flex;
	-webkit-align-items     : center;
	        align-items     : center;
	-webkit-justify-content : center;
	        justify-content : center;
	overflow        : hidden;
}

.project {
	height     : 180px;
	transition : -webkit-transform 200ms ease-in;
	transition : transform 200ms ease-in;
	transition : transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.project:hover {
	color            : #FFFFFF;
	color            : var(--color-white);
	background-color : #F64C08;
	background-color : var(--color-orange);
	-webkit-transform        : scale(1.05);
	        transform        : scale(1.05);
	display          : -webkit-flex;
	display          : flex;
	-webkit-justify-content  : center;
	        justify-content  : center;
	-webkit-align-items      : center;
	        align-items      : center;
	cursor           : pointer;
}

.project img {
	width  : 350px;
	height : 180px;
}

.project-content {
	padding : 10px;
}

.project-content > h4 {
	padding-bottom : 5px;
}

.project-content > p {
	font-size : 15px;
}

/* Product */


/* Experience */
.experience {
	margin     : 200px 0;
	color      : #FFFFFF;
	color      : var(--color-white);
	text-align : center;
}

.experience h1 {
	padding-bottom : 20px;
}

.experience-info {
	display             : -webkit-flex;
	display             : flex;
	-webkit-align-items         : center;
	        align-items         : center;
	-webkit-justify-content     : space-evenly;
	        justify-content     : space-evenly;
	width               : 80vw;
	height              : 240px;
	margin              : auto;
	box-shadow          : inset 0 0 0 2000px rgb(0 0 0 / 81%);
	background-image    : url(https://wallpaperaccess.com/full/1691700.jpg);
	background-position : 100% 100%;
	background-size     : cover;
}

.experience-box {
	display        : -webkit-flex;
	display        : flex;
	-webkit-flex-direction : column;
	        flex-direction : column;
	border         : 1px dashed #FFFFFF;
	border-radius  : 5px;
	padding        : 20px;
	width          : 120px;
	height         : 70px;
}

.experience-box.active {
	background-color : #F64C08;
	background-color : var(--color-orange);
	color            : #FFFFFF;
	color            : var(--color-white);
}

.experience-box h5 {
	font-family : "Crimson Text", sans-serif;
	font-family : var(--second-font);
	font-size   : 30px;
}

/* Experience */

/* contact */
.contact {
	display         : -webkit-flex;
	display         : flex;
	-webkit-flex-direction  : column;
	        flex-direction  : column;
	color           : #FFFFFF;
	color           : var(--color-white);
	-webkit-justify-content : center;
	        justify-content : center;
	-webkit-align-items     : center;
	        align-items     : center;
	margin          : 200px 0;
}

.contact-continer {
	border          : 1px solid #FFFFFF;
	border          : 1px solid var(--color-white);
	width           : 600px;
	height          : 200px;
	display         : -webkit-flex;
	display         : flex;
	-webkit-flex-direction  : column;
	        flex-direction  : column;
	-webkit-align-items     : center;
	        align-items     : center;
	-webkit-justify-content : center;
	        justify-content : center;
	padding         : 30px;
	margin-top      : 20px;
}

.contact-continer > p {
	font-size      : 20px;
	text-align     : left;
	padding-bottom : 30px;
}

.contact-continer > p > span {
	color : #F64C08;
	color : var(--color-orange);
}

.contact-icon > a > button > span > .MuiSvgIcon-root {
	color     : #FFFFFF;
	color     : var(--color-white);
	font-size : 30px;
}

.contact-icon > a > .MuiIconButton-root {
	color : #FFFFFF;
	color : var(--color-white);
}

/* contact */


@media (min-width : 1200px) {
	.container {
		max-width : 1140px;
		margin    : auto;
		padding   : 0 15px;
	}
}

@media (min-width : 992px) and (max-width : 1199px) {
	.container {
		max-width : 960px;
		margin    : auto;
		padding   : 0 15px;
	}
}

@media (max-width : 991px) {
	.container {
		max-width : 100%;
		margin    : auto;
		padding   : 0 15px;
	}

	.header {
		position : fixed;
		bottom   : 0;
		height   : 65px;
		top      : auto;
		top      : initial;
		width    : 100%;
	}

	.skill-img {
		display : none;
	}

	.skill-warpper {
		margin          : 100px auto;
		-webkit-justify-content : center;
		        justify-content : center;
	}

	.project-container-project > a {
		width : 50%;
	}
}

@media (max-width : 768px) {
	.contact-continer {
		width : 500px;
	}
}

@media (max-width : 576px) {
	.project-container-project {
		-webkit-flex-direction : column;
		        flex-direction : column;
	}

	.contact-continer {
		width : 100%;
	}
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

